import React, { useState, useEffect } from "react";
import { Wrapper, ButtonWrapper } from "./QuizCard.styles";

type Props = {
  question: string;
  correctAnswer: string;
  incorrectAnswers: string[];
  checkAnswer: (option: string) => void;
};

const QuizCard: React.FC<Props> = ({
  question,
  correctAnswer,
  incorrectAnswers,
  checkAnswer,
}) => {
  const [options, setOptions] = useState<string[]>([]);
  const [clicked, setClicked] = useState(false);
  const [chosenAnswer, setChosenAnswer] = useState("");

  const shuffleOptions = (array: string[]) =>
    array.sort(() => Math.random() - 0.5);

  useEffect(() => {
    setOptions(() => shuffleOptions([...incorrectAnswers, correctAnswer]));
    setClicked(false);
  }, [incorrectAnswers, correctAnswer]);

  // Curring
  const handleClick = (callback: any) => (answer: string) => {
    setClicked(true);
    setChosenAnswer(answer);
    // .textContent æˆ– .innerText æœƒå°‡ HTML ç‰¹æ®Šå­—å…ƒè½‰ç‚ºä¸€èˆ¬æ–‡å­— (&lt; è½‰ç‚º <)ï¼Œé€ æˆæ¯”å°éŒ¯èª¤
    callback(answer);
  };

  return (
    <Wrapper className="card">
      <h3 dangerouslySetInnerHTML={{ __html: question }} />
      <div className="card__options">
        {options.map((option) => (
          <ButtonWrapper
            key={option}
            correctOption={option === correctAnswer && clicked}
            isClicked={chosenAnswer === option}
          >
            <button
              value={option}
              disabled={clicked}
              onClick={({ currentTarget: { value } }) =>
                handleClick(checkAnswer)(value)
              }
            >
              <span dangerouslySetInnerHTML={{ __html: option }}></span>
            </button>
          </ButtonWrapper>
        ))}
      </div>
    </Wrapper>
  );
};
export default QuizCard;
